import React, { Component } from 'react';

import Header from 'components/shared/Header';
import Footer from 'components/shared/Footer';
// import CatalogItemCard from 'components/catalog/CatalogItemCard';

import 'css/style.css';
import 'css/style-order.css';
import 'css/style-catalogue.css';
import 'css/style-favorite.css';

class Favorite extends Component {
  render() {
    return (
      <div className="container">
        <Header />

        <div className="wrapper wrapper_favorite">
          <div className="site-path">
            <ul className="site-path__items">
              <li className="site-path__item"><a href="index.html">Главная</a></li>
              <li className="site-path__item"><a href="favorite.html">Избранное</a></li>
            </ul>
          </div>
          <main className="product-catalogue product-catalogue_favorite">
            <section className="product-catalogue__head product-catalogue__head_favorite">
              <div className="product-catalogue__section-title">
                <h2 className="section-name">В вашем избранном</h2><span className="amount amount_favorite"> 99 товаров</span>
              </div>
              <div className="product-catalogue__sort-by">
                <p className="sort-by">Сортировать</p>
                <select id="sorting">
                  <option value="">по дате добавления</option>
                  <option value="">по размеру</option>
                  <option value="">по производителю</option>
                </select>
              </div>
            </section>
            <section className="product-catalogue__item-list product-catalogue__item-list_favorite">

              {/* <CatalogItemCard /> */}

            </section>
            
            <div className="product-catalogue__pagination">
              <div className="page-nav-wrapper">
                <a className="angle-back">
                  <span className="visually-hidden">Предыдущая страница</span>
                </a>
                <ul className="catalogue-pagination__list">
                  <li className="active"><a href="">1</a></li>
                  <li><a href="">2</a></li>
                  <li><a href="">3</a></li>
                  <li><a href="">4</a></li>
                  <li><a href="">5</a></li>
                  <li><a href="">...</a></li>
                  <li><a href="">99</a></li>
                </ul>
                <a className="angle-forward">
                  <span className="visually-hidden">Следующая страница</span>
                </a>
              </div>
            </div>
          </main>
        </div>

        <Footer />
      </div>
    );
  }
}

export default Favorite;



