import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class CatalogItemCard extends Component {
  render() {
    const data = this.props.data;

    return (
      <Link className="item-list__item-card item" to={`/product/${data.id}`}>
        <div className="item-pic">
          <img className="item-pic-1" src={data.images[0]} alt={data.title} />
          <div className="product-catalogue__product_favorite">
            <p></p>
          </div>
          <div className="arrow arrow_left"></div>
          <div className="arrow arrow_right"></div>
        </div>
        <div className="item-desc">
          <h4 className="item-name">{data.title}</h4>
          <p className="item-producer">Производитель:&nbsp;
            <span className="producer">{data.brand}</span>
          </p>
          <p className="item-price">{data.price}</p>
          <div className="sizes">
            <p className="sizes__title">Размеры в наличии:</p>
            <p className="sizes__avalible">36, 37, 38, 39, 40, 41, 42</p>
          </div>
        </div>
      </Link>
    );
  }
}

export default CatalogItemCard;



